* {
  /* margin: 0; */
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-container {
  margin-left: 5%;
  margin-right: 5%;
  padding-bottom: 40px;
}

.nav-bar {
  background: #833ab4; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #fcb045,
    #fd1d1d,
    #833ab4
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  color: white;
}

.nav-bar-container {
  margin-left: 5%;
  margin-right: 5%;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-bar-container h1 {
  font-family: "Pacifico", cursive;
}

.nav-menu {
  display: flex;
  justify-content: space-between;
  list-style: none;
  width: 450px;
}

.nav-menu li {
  border-radius: 5px;
  padding: 3px 15px 8px 15px;
  background-color: transparent;
}

.nav-menu li a {
  cursor: pointer;
  text-decoration: none;
  color: white;
}

.nav-menu li:hover {
  background-color: rgba(0, 0, 0, 0.4);
}

.cont-contente {
  display: flex;
  justify-content: center;
  align-items: stretch;
  width: 50%;
  margin: auto;
  margin-top: 50px;
  padding-bottom: 30px;
}

.user-info {
  text-align: center;
  border-right: 1px dotted black;
  padding-right: 40px;
}

.user-info h2,
.user-contacts h2 {
  font-weight: 800;
  font-size: 29px;
  color: #9a3497;
}

.user-image img {
  border-radius: 128px;
  height: 256px;
}

.user-info p {
  text-align: center;
  width: 350px;
}

.user-contacts {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* height: 192px; */
  width: 100%;
  padding-left: 40px;
}

.contact-info {
  font-size: 1.5rem;
  font-weight: 500;
}

.contact-info svg {
  font-size: 1.8 rem;
  color: #8d38a8;
  margin-right: 15px;
}

.contact-info span {
  color: #1d73e4;
  text-decoration: underline;
}

.home-container {
  /* height: 93vh; */
  background: #833ab4; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #fcb045,
    #fd1d1d,
    #833ab4
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  /* position: absolute;
  left: 0;
  right: 0; */
}

.avatar {
  text-align: center;
  padding-top: 15px;
  padding-bottom: 15px;
}

.avatar img {
  border-radius: 128px;
  height: 256px;
}

.banner {
  margin: auto;
  width: 75%;
  background-color: #000000a6;
  color: white;
  border-radius: 10px;
  text-align: center;
  padding-top: 10px;
  padding-bottom: 20px;
}

.banner h1 {
  font-size: 66px;
  font-weight: bold;
}

.banner hr {
  border-bottom: 8px dotted white;
  border-top: none;
  width: 50%;
  margin: auto;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 25%;
  margin: auto;
}

.social-links svg {
  color: white;
  font-size: 3rem;
}

.resume-contente {
  margin-top: -50px;
  margin-bottom: 30px;
  padding-top: 50px;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.resume-contente .exp-sk {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 50px;
  box-shadow: 0 1px 8px -2.5px black;
  padding-left: 20px;
  padding-right: 20px;
  flex: 3 1;
}

.resume-contente .skills {
  margin-top: 50px;
  margin-right: 30px;
  box-shadow: 0 1px 8px -2.5px black;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  flex: 1 1;
}

.experiences,
.education {
  flex: 1 1;
}

.experiences {
  margin-right: 20px;
}

.skills h2,
.experiences h2,
.education h2 {
  text-align: center;
}

.skill {
  padding: 2px;
  border: 2px solid #e1e1e1;
  margin-top: 8px;
  border-radius: 5px;
}

.level {
  height: 4px;
}

.level-beginer {
  background-color: rgba(206, 6, 6);
  width: 25%;
}

.level-medium {
  background-color: rgb(241, 173, 46);
  width: 50%;
}

.level-advanced {
  background-color: rgba(0, 255, 34);
  width: 80%;
}

.level-expert {
  background-color: rgba(7, 160, 19, 0.945);
  width: 90%;
}

.exp-item {
  border-left: 2px solid #cf9c63;
  position: relative;
  padding: 2px 20px 10px;
  /* margin-left: 20px; */
  margin-bottom: 30px;
  background-color: #fff;
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.2);
}

.exp-item .title {
  display: block;
  margin: 0;
  font-size: 16px;
  line-height: 1.2em;
}

.exp-item .location {
  padding-left: 5px;
  border-left: 1px solid #e0e0e0;
  margin-left: 5px;
}

